.AddNotification {
  background-color: #fff;
  box-shadow: 1px 1px 2px rgba(#ddd, 0.8s);
  margin: auto;

  display: grid;
  grid-template-rows: repeat(6, max-content);
  grid-row-gap: 2rem;
  padding: 1rem;

  h1 {
    color: var(--color-primary);
  }
  .form-group {
    &-label {
      font-size: 1.2rem;
      font-weight: 800;
  
      display: block;
    }
  
    & select {
      background-color: rgba(#ccc, 0.4);
      border: none;
      padding: 0.8rem;
      border-radius: 0.4rem;
    }
  
    &-option {
      font-size: 1rem;
      font-weight: 500;
      display: grid;
      grid-template-columns: max-content max-content;
      align-items: center;
      grid-column-gap: 10px;
    }
  
    &-choice {
      display: grid;
      grid-template-columns: 15% 15% 15%;
  
      & > label {
        font-size: 0.8rem;
        font-weight: normal;
        display: grid;
        grid-template-columns: max-content max-content;
        align-items: center;
        grid-column-gap: 10px;
      }
    }
  
    &__input {
      background-color: rgba(#f3f3f3, 0.9);
      border: none;
      padding: 0.8rem;
      border-radius: 0.4rem;
  
      &:disabled {
        background-color: rgba(#ccc, 0.9);
      }
  
      &--fullwidth {
        width: 100%;
      }
    }
  
    textarea {
      width: 100%;
      height: 150px;
      background-color: rgba(#f3f3f3, 0.9);
      border: 1px solid #ccc;
      resize: none;
      padding: 1rem 1rem;
      font-family: inherit;
    }
  }
  
  .btn {
    cursor: pointer;
    width: 100%;
  }
  
  .btn-submit {
    padding: 0.7rem;
    border-radius: 8px;
    background-color: #fff;
    border: 1px solid black;
    font-weight: 800;
    color: var(--color-primary);
    transition: all ease-in 0.3s;
  
    &:hover {
      background-color: var(--color-primary);
      color: #fff;
    }
  
    &:focus {
      transform: scale(0.9);
      -webkit-transform: scale(0.9);
    }
  }
}

